}

header {
  background-color: #004b37;
}

header .header__section {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100.1%;
  height: 70px;
  z-index: 999;
  background-color: #004b37;
  transition: 0.3s ease;
}

header .header__section.darkNav::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background-color: rgba(0, 0, 0, 0.849);
}

header .header__section.navShadow {
  box-shadow: 0px 5.69882px 9.49804px rgba(48, 182, 113, 0.098);
}

header .header__section .header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
}

header .header__section .header__navbar .header__navLinks_btns {
  display: flex;
  align-items: center;
}

header .header__section .header__navbar .header__navLinks_btns .navLinks {
  display: flex;
  margin-left: 25.5px;
}

header .header__section .header__navbar .header__navLinks_btns .navLinks ul {
  display: flex;
}

header .header__section .header__navbar .header__navLinks_btns .navLinks ul li a {
  color: #ffffff;
  font-size: 15px;
  font-weight: 400;
  padding: 8px 22.5px;
  position: relative;
}

header .header__section .header__navbar .header__navLinks_btns .navLinks ul li a::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0;
  height: 3px;
  background-color: #ffffff;
  transition: 0.2s ease;
}

header .header__section .header__navbar .header__navLinks_btns .navLinks ul li a:hover::after {
  width: 30px;
}

header .header__section .header__navbar .header__navLinks_btns .navLinks .separately_links li a {
  padding: 8px 15px;
}

header .header__section .header__navbar .header__btns {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

header .header__section .header__navbar .header__btns .header_btn {
  padding: 8px 36px;
  font-size: 15px;
  background-color: #F4FBF9;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: 0.2s ease;
}

header .header__section .header__navbar .header__btns .header_btn:hover {
  background-color: #192F44;
  color: #ffffff;
}

header .header__section .header__navbar .header__btns .login_btn {
  background-color: transparent;
  color: #ffffff;
  padding: 10px 20px;
  border: 2px solid rgba(255, 255, 255, 0);
}

header .header__section .header__navbar .header__btns .login_btn:hover {
  border: 2px solid #ffffff;
  background: none;
  color: #ffffff;
}

header .header__section .header__navbar .burger .bar {
  width: 27px;
  height: 3px;
  margin: 5px 0;
  background-color: #ffffff;
}

header .header__section .header__navbar .burger {
  cursor: pointer;
  display: none;
  margin-left: 20px;
}

header .header__section .header__navbar .navside {
  position: fixed;
  top: 0;
  right: 0;
  width: 350px;
  max-width: 100%;
  height: 100vh;
  padding: 0 20px;
  transform: translateX(120%);
  background-color: #193844;
  z-index: 1600;
  transition: 0.3s ease;
}

header .header__section .header__navbar .navside.showNav {
  transform: translateX(0);
}

header .header__section .header__navbar .navside .closeNav {
  position: absolute;
  top: 15px;
  right: 23px;
  cursor: pointer;
  width: 40px;
  height: 40px;
}

header .header__section .header__navbar .navside .closeNav .bar {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 25px;
  height: 4px;
  background-color: #ffffff;
}

header .header__section .header__navbar .navside .closeNav .bar:nth-child(2) {
  transform: rotate(45deg) translate(-10px, 7px);
}

header .header__section .header__navbar .navside ul {
  margin-top: 100px;
}

header .header__section .header__navbar .navside ul li a {
  display: block;
  padding: 10px 0;
  color: #ffffff;
  position: relative;
  transition: 0.2s ease;
  font-size: 15px;
}

header .header__section .header__navbar .navside ul li a::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -7px;
  width: 0;
  height: 3px;
  background-color: #00CA90;
  transform: translate(-50%, -50%) rotate(90deg);
  transition: 0.2s ease;
}

header .header__section .header__navbar .navside ul li a:hover {
  padding-left: 7px;
}

header .header__section .header__navbar .navside ul li a:hover::after {
  width: 25px;
}

header .header__section .header__navbar .navside .separately_links {
  margin-top: 10px;
}

header .header__section .header__navbar .navside .navside_btns {
  display: flex;
  align-items: center;
  column-gap: 10px;
  margin-top: 30px;
}

header .header__section .header__navbar .navside .navside_btns .header_btn {
  padding: 8px 36px;
  font-size: 15px;
  background-color: #F4FBF9;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: 0.2s ease;
}

header .header__section .header__navbar .navside .navside_btns .header_btn:hover {
  background-color: #004b37;
  color: #ffffff;
}

header .header__section .header__navbar .navside .navside_btns .login_btn {
  background-color: transparent;
  color: #ffffff;
  padding: 10px 20px;
  border: 2px solid rgba(255, 255, 255, 0);
}

header .header__section .header__navbar .navside .navside_btns .login_btn:hover {
  border: 2px solid #ffffff;
  background: none;
