.toUpArrow {
  position: fixed;
  top: 120%;
  right: 1%;
  transform: translate(-50%, -50%);
  z-index: 500;
  transition: 0.3s ease;
}

.toUpArrow.showArrow {
  top: 90%;
}

.toUpArrow a {
  fill: #ffffff;
  background-color: #192F44;
  border-radius: 5px;
  padding: 10px;
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
